<template>
  <div class="hello">
    <van-nav-bar
      title="申请贷款"
      left-arrow
      @click-left="handleBack"
    />
    <div class="container">
      <div class="containerHeader">
        <div class="containerHeaderTitle">根据所回答的问题，预计可贷</div>
        <div class="containerHeaderMoney">
          <span>{{money}}</span>元
        </div>
      </div>

      <div class="warningContainer">
        <img src="../assets/success.png" alt=""/>
        <div class="warningText">具体的实际贷款金额由银行进行评定，请完善以下申请信息</div>
      </div>

      <div class="chooseContainer">
        <div class="chooseTitle">
          <div class="chooseTitleNav"></div>
          贷款申请资料（5/5题）
        </div>

        <div class="formContainer">
          <div class="formList" v-show="currentIndex===0||currentIndex===3">
            <div class="formItem">
              <label>姓名</label>
              <div class="formInput">
                <van-field v-model="name" style="padding: 0.625rem 0.5rem;" placeholder="点击这里输入姓名"
                           input-align="right"/>
              </div>
            </div>
            <div class="formItem">
              <label>手机号</label>
              <div class="formInput">
                <van-field v-model="phone" style="padding: 0.625rem 0.5rem;" placeholder="点击这里输入手机号"
                           input-align="right"/>
              </div>
            </div>
            <div class="formItem">
              <label>身份证</label>
              <div class="formInput">
                <van-field v-model="status" style="padding: 0.625rem 0.5rem;" placeholder="点击这里输入身份证号"
                           input-align="right"/>
              </div>
            </div>
            <div class="formItem1" @click="handleShow(true)">
              <label>申请贷款地点</label>
              <div class="formValue">
                <span style="color: #969799" v-show="addressIndex===-1">选择</span>
                {{addressValue}}
              </div>
              <img src="../assets/arrow.png" alt="">
            </div>
            <div class="formItem1">
              <label>经营地址/工作地址</label>
            </div>
            <div class="formItem2">
              <van-field v-bind:autosize='autoSize' type="textarea" style="padding: 0.625rem 0;" v-model="address"
                         placeholder="点击这里输入经营地址/工作地址"/>
            </div>
          </div>
        </div>

        <van-popup v-model="show" position="bottom" :overlay="true">
          <van-picker
            show-toolbar
            title="申请贷款地点"
            :columns="columns"
            @cancel="handleShow(false)"
            @confirm="onConfirm"
          />
        </van-popup>

      </div>
    </div>
    <div class="nextBtn">
      <van-button type="danger" @click="handleSubmit">提交资料</van-button>
    </div>
  </div>
</template>

<script>
  import Vue from 'vue';
  import {Button, NavBar, Popup, Picker, Field, Toast} from 'vant';
  import service from '../utils/service';

  Vue.use(Button);
  Vue.use(NavBar);
  Vue.use(Popup);
  Vue.use(Picker);
  Vue.use(Field);
  Vue.use(Toast);
  export default {
    name: 'Apply',
    data() {
      return {
        show: false,
        currentIndex: 0,
        columns: ['郑州', '开封', '洛阳', '南阳'],
        loanIndex: -1,
        loanValue: '',
        money: '',
        name: '',
        phone: '',
        status: '',
        address: '',
        addressIndex: -1,
        addressValue: '',
        autoSize: true
      }
    },
    mounted: function () {
      const money = service.getData('money');
      if (!money) {
        if (!money || money <= 0) {
          this.$toast({
            duration: 1000,       // 持续展示 toast
            message: '请重新获取贷款金额'
          });
          this.$router.push({
            path: '/index'
          })
        }
      }
      this.money = money;
    },
    methods: {
      /*
      处理返回事件
       */
      handleBack: function () {
        this.$router.go(-1);
      },
      /**
       * 处理选择事件
       * @param index
       */
      handleChoose(index) {
        this.currentIndex = index;
      },

      /**
       * 显示隐藏弹框
       */
      handleShow: function (visible) {
        this.show = visible;
      },

      /**
       * 确定选择
       */
      onConfirm(value, index) {
        this.show = false;
        this.addressIndex = index;
        this.addressValue = value;
      },

      /**
       * 提交贷款
       */
      handleSubmit() {
        const name = this.name;
        const phone = this.phone;
        const status = this.status;
        const addressValue = this.addressValue;
        const addressIndex = this.addressIndex;
        const address = this.address;
        const money = this.address;
        if (!name) {
          this.$toast({
            duration: 1000,       // 持续展示 toast
            message: '请输入姓名'
          });
          return;
        }
        if (!phone) {
          this.$toast({
            duration: 1000,       // 持续展示 toast
            message: '请输入手机号'
          });
          return;
        }
        const regPhone = /^1([38][0-9]|4[579]|5[0-3,5-9]|6[6]|7[0135678]|9[89])\d{8}$/;
        if (regPhone.test(phone) === false) {
          this.$toast({
            duration: 1000,       // 持续展示 toast
            message: '手机号有误'
          });
          return;
        }
        if (!status) {
          this.$toast({
            duration: 1000,       // 持续展示 toast
            message: '请输入身份证号'
          });
          return;
        }
        const regIdNo = /(^\d{15}$)|(^\d{18}$)|(^\d{17}(\d|X|x)$)/;
        if (regIdNo.test(status) === false) {
          this.$toast({
            duration: 1000,       // 持续展示 toast
            message: '身份证号有误'
          });
          return;
        }
        if (addressIndex < 0) {
          this.$toast({
            duration: 1000,       // 持续展示 toast
            message: '请选择贷款地点'
          });
          return;
        }
        if (!address) {
          this.$toast({
            duration: 1000,       // 持续展示 toast
            message: '请输入经营地址/工作地址'
          });
          return;
        }
        const data = {
          name,
          phone,
          status,
          addressValue,
          address
        };
        this.$toast({
          type: 'loading',
          message: '数据提交中',
          duration: 0
        });
        service.doPost('api/submit', data).then(result => {
          console.log(result);
          if (result.code === 1) {
            service.removeData('money');
            this.$router.push({
              name: 'Success'
            });
            this.$toast.clear();
          } else {
            this.$toast({
              duration: 1000,       // 持续展示 toast
              message: result.message
            });
          }
        });
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .van-nav-bar .van-icon {
    color: #2c3e50;
    font-size: 1.2rem;
  }

  .container {

  }

  .nextBtn {
    margin-top: 1.2rem;
  }

  .nextBtn .van-button--danger {
    color: #fff;
    background-color: #ef5066;
    border: 0.0625rem solid #ef5066;
    width: 100%;
    height: 3rem;
    line-height: 3rem;
  }

  .containerHeader {
    width: calc(100% - 2rem);
    background: #fff;
    padding: 2rem 1rem 2rem 1rem;
    background: linear-gradient(to right, #ef5066, #f69981);
    color: #fff;
  }

  .containerHeaderTitle {
    font-size: 0.95rem;
  }

  .containerHeaderMoney {
    margin-top: 0.6rem;
    font-size: 0.95rem;
  }

  .containerHeaderMoney span {
    font-size: 2.3rem;
    margin-right: 0.4rem;
  }

  .warningContainer {
    background: rgb(253, 252, 221);
    font-size: 0.85rem;
    height: 2.8rem;
    line-height: 2.8rem;
    padding: 0 0.5rem;
    display: flex;
    color: rgb(248, 181, 92);
  }

  .warningContainer img {
    width: 1.4rem;
    height: 1.4rem;
    margin-top: 0.6rem;
  }

  .warningContainer .warningText {
    margin-left: 0.4rem;
    -ms-text-overflow: ellipsis;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
  }

  .chooseContainer {
    background: #fff;
    -webkit-border-radius: 0.3rem;
    -moz-border-radius: 0.3rem;
    border-radius: 0.3rem;
    padding: 1rem 0.6rem;
    text-align: left;
  }

  .chooseTitle {
    font-size: 1rem;
    color: rgba(0, 0, 0, .87);
    font-weight: 500;
    -ms-text-overflow: ellipsis;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
    height: 1.6rem;
    line-height: 1.6rem;
    display: flex;
  }

  .chooseTitleNav {
    width: 0.4rem;
    height: 1.3rem;
    background: #ef5066;
    margin-right: 0.5rem;
    -webkit-border-radius: 0.2rem;
    -moz-border-radius: 0.2rem;
    border-radius: 0.2rem;
  }

  .formItem {
    display: flex;
    height: 3rem;
    line-height: 3rem;
  }

  .formItem:first-child {
    margin-top: 0.5rem;
  }

  .formItem label {
    width: 5rem;
    font-size: 0.9rem;
    color: rgba(0, 0, 0, 0.87);
  }

  .formItem .formValue {
    width: calc(100% - 6.1rem);
    text-align: right;
    font-size: 0.9rem;
    -ms-text-overflow: ellipsis;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
  }

  .formItem img {
    width: 1.1rem;
    height: 1.1rem;
    margin-top: 0.85rem;
  }

  .formItem .formInput {
    width: calc(100% - 5rem);
    margin-top: 0.15rem;
  }

  .formItem1 {
    display: flex;
    height: 3rem;
    line-height: 3rem;
  }

  .formItem1:first-child {
    margin-top: 0.5rem;
  }

  .formItem1 label {
    width: 8.5rem;
    font-size: 0.9rem;
    color: rgba(0, 0, 0, 0.87);
  }

  .formItem1 .formValue {
    width: calc(100% - 9.6rem);
    text-align: right;
    font-size: 0.9rem;
    -ms-text-overflow: ellipsis;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
  }

  .formItem1 img {
    width: 1.1rem;
    height: 1.1rem;
    margin-top: 0.85rem;
  }

  .formItem1 .formInput {
    width: calc(100% - 8.5rem);
    margin-top: 0.15rem;
  }

</style>



