/*
    data = { houseLoan:3000, lastHouseLoan:3000, insuranceMoney:3000, accumulationFundBase:4000, question:1, answer:1}
    houseLoan: 房贷月供金额
    lastHouseLoan: 最后一个月房贷月供金额
    insuranceMoney: 年缴保费金额
    accumulationFundBase: 公积金缴存基数
    question: 第几个问题 1~4
    answer: 选择的答案1~*
  */

function getMoneyForLoanable(data) {
  if (data.question === 1) {
    if (!data.houseLoan) {
      return 'houseLoan缺失'
    }
    switch (data.answer) {
      case 1:
        return data.houseLoan * 45;
        break;
      case 2:
        return data.houseLoan * 70;
        break;
      default:
        return '数据有误'
    }
  }
  if (data.question === 2) {
    if (!data.insuranceMoney) {
      return 'hinsuranceMoney缺失'
    }
    switch (data.answer) {
      case 1:
        return data.insuranceMoney * 20;
        break;
      case 2:
        return data.insuranceMoney * 30;
        break;
      case 3:
        return data.insuranceMoney * 40;
        break;
      default:
        return '数据有误'
    }
  }
  if (data.question === 3) {
    if (!data.accumulationFundBase) {
      return 'accumulationFundBase缺失'
    }
    switch (data.answer) {
      case 1:
        return data.accumulationFundBase * 40;
        break;
      case 2:
        return data.accumulationFundBase * 60;
        break;
      default:
        return '数据有误'
    }
  }
  if (data.question === 4) {
    if (!data.houseLoan) {
      return 'houseLoan缺失'
    }
    switch (data.answer) {
      case 1:
        return data.houseLoan * 80;
        break;
      case 2:
        return data.houseLoan * 100;
        break;
      case 3:
        return data.houseLoan * 120;
        break;
      default:
        return '数据有误'
    }
  }

  if (data.question === 5) {
    if (!data.lastHouseLoan) {
      return 'lastHouseLoan缺失'
    }
    return data.lastHouseLoan*45;
  }
}

export default getMoneyForLoanable;



