<template>
  <div class="hello">
    <van-nav-bar
      title="申请贷款"
      left-arrow
      @click-left="handleBack"
    />
    <div class="container">
      <div class="containerHeader">
        <div class="productInfo">
          <img src="../assets/peace1.png" alt=""/>
          <div class="productName">平安银行-新一贷</div>
        </div>
        <div class="productIntroduce">
          消费备用金首选产品！信用借贷最快2小时审批放款，分期还，利率低，首批次放款金额最高可达50万元。
        </div>
      </div>
      <div class="chooseContainer">
        <div class="chooseTitle">
          选择现有情况，估算贷款金额
        </div>
        <div class="chooseList">
          <div class="chooseListItem" v-for="(item,index) in chooseList" v-bind:key="index">
            {{item}}
          </div>
        </div>
        <div class="btnList">
          <van-button v-for="(item,index) in btnList" type="default"
                      v-bind:key="index"
                      @click="handleChoose(index)"
                      v-bind:class="[currentIndex=== index? 'active' : '']">
            {{item}}
          </van-button>
        </div>

        <div class="formContainer">
          <div class="formList" v-show="currentIndex===0">
            <div class="formItem" @click="handleShow(true)">
              <label>房贷期限</label>
              <div class="formValue">
                <span style="color: #969799" v-show="loanIndex===-1">选择</span>
                {{loanValue}}
              </div>
              <img src="../assets/arrow.png" alt="">
            </div>
            <div class="formItem">
              <label>月供金额(元)</label>
              <div class="formInput">
                <van-field type="number" v-model="money" placeholder="点击这里输入金额" input-align="right"/>
              </div>
            </div>
          </div>

          <div class="formList" v-show="currentIndex===3">
            <div class="formItem" @click="handleShow3(true)">
              <label>房贷期限</label>
              <div class="formValue">
                <span style="color: #969799" v-show="loanIndex1===-1">选择</span>
                {{loanValue1}}
              </div>
              <img src="../assets/arrow.png" alt="">
            </div>
            <div class="formItem">
              <label>月供金额(元)</label>
              <div class="formInput">
                <van-field type="number" v-model="money1" placeholder="点击这里输入金额" input-align="right"/>
              </div>
            </div>
          </div>

          <div class="formList" v-show="currentIndex===1">
            <div class="formItem1" @click="handleShow4(true)">
              <label>准入公司</label>
              <div class="formValue">
                <span style="color: #969799" v-show="companyIndex===-1">选择</span>
                {{companyValue}}
              </div>
              <img src="../assets/arrow.png" alt="">
            </div>
            <div class="formItem1" @click="handleShow1(true)">
              <label>保单生效与缴费次数</label>
              <div class="formValue">
                <span style="color: #969799" v-show="insuranceIndex===-1">选择</span>
                {{insuranceValue}}
              </div>
              <img src="../assets/arrow.png" alt="">
            </div>
            <div class="formItem1">
              <label>年缴保费金额(元)</label>
              <div class="formInput">
                <van-field type="number" v-model="insuranceMoney" placeholder="点击这里输入金额" input-align="right"/>
              </div>
            </div>
          </div>

          <div class="formList" v-show="currentIndex===2">
            <div class="formItem" @click="handleShow2(true)">
              <label>缴存基数</label>
              <div class="formValue">
                <span style="color: #969799" v-show="goldIndex===-1">选择缴存基数</span>
                {{goldValue}}
              </div>
              <img src="../assets/arrow.png" alt="">
            </div>
            <div class="formItem1">
              <label>缴存金额</label>
              <div class="formInput">
                <van-field type="number" v-model="goldMoney" placeholder="点击这里输入金额" input-align="right"/>
              </div>
            </div>
          </div>
        </div>

        <div class="formList" v-show="currentIndex===4">
          <div class="formItem1">
            <label>最后月供金额(元)</label>
            <div class="formInput">
              <van-field type="number" v-model="endMoney" placeholder="点击这里输入金额" input-align="right"/>
            </div>
          </div>
        </div>

        <van-popup v-model="show" position="bottom" :overlay="true">
          <van-picker
            show-toolbar
            title="房贷期限"
            :columns="columns"
            @cancel="handleShow(false)"
            @confirm="onConfirm"
          />
        </van-popup>

        <van-popup v-model="show1" position="bottom" :overlay="true">
          <van-picker
            show-toolbar
            title="保单生效与缴费次数"
            :columns="columns1"
            @cancel="handleShow1(false)"
            @confirm="onConfirm1"
          />
        </van-popup>

        <van-popup v-model="show2" position="bottom" :overlay="true">
          <van-picker
            show-toolbar
            title="缴存金额"
            :columns="columns2"
            @cancel="handleShow2(false)"
            @confirm="onConfirm2"
          />
        </van-popup>

        <van-popup v-model="show3" position="bottom" :overlay="true">
          <van-picker
            show-toolbar
            title="房贷期限"
            :columns="columns3"
            @cancel="handleShow3(false)"
            @confirm="onConfirm3"
          />
        </van-popup>

        <van-popup v-model="show4" position="bottom" :overlay="true">
          <van-picker
            show-toolbar
            title="准入公司"
            :columns="columns4"
            @cancel="handleShow4(false)"
            @confirm="onConfirm4"
          />
        </van-popup>
      </div>
    </div>
    <div class="nextBtn">
      <van-button type="danger" @click="handleNext">下一步</van-button>
    </div>
  </div>
</template>

<script>
  import Vue from 'vue';
  import {Button, NavBar, Popup, Picker, Field, Toast} from 'vant';
  import getMoneyForLoanable from '../utils/count';
  import service from '../utils/service';

  Vue.use(Button);
  Vue.use(NavBar);
  Vue.use(Popup);
  Vue.use(Picker);
  Vue.use(Field);
  Vue.use(Toast);
  export default {
    name: 'Index',
    data() {
      return {
        chooseList: [
          "A.有房贷",
          "B.有生效满两年寿险保单(需工作地/居住地有房产)",
          "C.有缴满两年的公积金(需工作地/居住地有房产)",
          "D.有房贷且同时拥有生效三年以上平安寿险保单或缴有两年以上公积金",
          "E.房贷已结清且在一年以内",
          "F.以上情况都没有"
        ],
        btnList: [
          "A",
          "B",
          "C",
          "D",
          "E",
          "F"
        ],
        show: false,
        show1: false,
        show2: false,
        show3: false,
        show4: false,
        currentIndex: -1,
        columns: ['已还款12个月以内', '已还款12个月以上', '已还款36个月以上'],
        columns1: ['生效2年以内', '生效2年且有3次缴费记录', '生效3年且有4次缴费记录', '生效5年且有6次缴费记录'],
        columns2: ['缴费基数低于4000元', '缴存基数为4000-8000（不含）元', '缴存基数为8000元及以上'],
        columns3: ['已还款1-3年', '已还款3-5年', '已还款5年及以上'],
        columns4: ['平安人寿', '中国人寿', '新华人寿', '泰康人寿', '太平洋人寿', '中国人民人寿保险', '太平人寿', '友邦人寿', '富邦生命人寿',
          '阳光人寿', '安联人寿', '工银安盛人寿', '大都会保险', '天安人寿', '中英人寿', '民生人寿', '中邮人寿', '中荷人寿', '华夏人寿'],
        loanIndex: -1,
        loanValue: '',
        money: '',
        loanIndex1: -1,
        loanValue1: '',
        money1: '',
        insuranceIndex: -1,
        insuranceValue: '',
        insuranceMoney: '',
        goldIndex: -1,
        goldValue: '',
        goldMoney: '',
        companyIndex: -1,
        companyValue: '',
        endMoney: ''
      }
    },
    methods: {
      /*
      处理返回事件
       */
      handleBack: function () {

      },
      /**
       * 处理选择事件
       * @param index
       */
      handleChoose(index) {
        this.currentIndex = index;
      },

      /**
       * 显示隐藏弹框
       */
      handleShow: function (visible) {
        this.show = visible;
      },

      /**
       * 确定选择
       */
      onConfirm(value, index) {
        this.show = false;
        this.loanIndex = index;
        this.loanValue = value;
      },

      /**
       * 显示隐藏弹框
       */
      handleShow1: function (visible) {
        this.show1 = visible;
      },

      /**
       * 确定选择
       */
      onConfirm1(value, index) {
        this.show1 = false;
        this.insuranceIndex = index;
        this.insuranceValue = value;
      },

      /**
       * 显示隐藏弹框
       */
      handleShow2: function (visible) {
        this.show2 = visible;
      },

      /**
       * 确定选择
       */
      onConfirm2(value, index) {
        this.show2 = false;
        this.goldIndex = index;
        this.goldValue = value;
      },

      /**
       * 显示隐藏弹框
       */
      handleShow3: function (visible) {
        this.show3 = visible;
      },

      /**
       * 确定选择
       */
      onConfirm3(value, index) {
        this.show3 = false;
        this.loanIndex1 = index;
        this.loanValue1 = value;
      },

      /**
       * 显示隐藏弹框
       */
      handleShow4: function (visible) {
        this.show4 = visible;
      },

      /**
       * 确定选择
       */
      onConfirm4(value, index) {
        this.show4 = false;
        this.companyIndex = index;
        this.companyValue = value;
      },
      /**
       * 跳转下一步
       */
      handleNext() {
        const currentIndex = this.currentIndex;
        const loanIndex = this.loanIndex;
        const money = this.money;
        const endMoney = this.endMoney;
        const loanIndex1 = this.loanIndex1;
        const money1 = this.money1;
        const insuranceIndex = this.insuranceIndex;
        const insuranceMoney = this.insuranceMoney;
        const goldIndex = this.goldIndex;
        const goldMoney = this.goldMoney;
        const companyIndex = this.companyIndex;
        if (currentIndex < 0) {
          this.$toast({
            duration: 1000,       // 持续展示 toast
            message: '请选择一种情况'
          });
          return;
        }

        const data = {};

        if (currentIndex === 0) {
          if (loanIndex < 0) {
            this.$toast({
              duration: 1000,       // 持续展示 toast
              message: '请选择房贷期限'
            });
            return;
          }
          if (loanIndex === 0) {
            this.handleGoBackApp();
            return;
          }
          if (!money || money <= 0) {
            this.$toast({
              duration: 1000,       // 持续展示 toast
              message: '请输入月供金额'
            });
            return;
          }
          data.question = 1;
          data.answer = loanIndex;
          data.houseLoan = money;
        }

        if (currentIndex === 1) {
          if (companyIndex < 0) {
            this.$toast({
              duration: 1000,       // 持续展示 toast
              message: '请选择准入公司'
            });
            return;
          }

          if (insuranceIndex < 0) {
            this.$toast({
              duration: 1000,       // 持续展示 toast
              message: '请选择保单生效与缴费次数'
            });
            return;
          }
          if (insuranceIndex === 0) {
            this.handleGoBackApp();
            return;
          }
          if (!insuranceMoney || insuranceMoney <= 0) {
            this.$toast({
              duration: 1000,       // 持续展示 toast
              message: '请输入年缴保费金额'
            });
            return;
          }
          data.question = 2;
          data.answer = insuranceIndex;
          data.insuranceMoney = insuranceMoney;
        }

        if (currentIndex === 2) {
          if (goldIndex < 0) {
            this.$toast({
              duration: 1000,       // 持续展示 toast
              message: '请选择缴存基数'
            });
            return;
          }

          if (!goldMoney || goldMoney <= 0) {
            this.$toast({
              duration: 1000,       // 持续展示 toast
              message: '请输入缴存金额'
            });
            return;
          }

          data.question = 3;
          data.answer = goldIndex;
          data.accumulationFundBase = goldMoney;
        }

        if (currentIndex === 3) {
          if (loanIndex1 < 0) {
            this.$toast({
              duration: 1000,       // 持续展示 toast
              message: '请选择房贷期限'
            });
            return;
          }
          if (!money1 || money1 <= 0) {
            this.$toast({
              duration: 1000,       // 持续展示 toast
              message: '请输入月供金额'
            });
            return;
          }

          data.question = 4;
          data.answer = currentIndex;
          data.houseLoan = money1;
        }

        if (currentIndex === 4) {
          if (!endMoney || endMoney <= 0) {
            this.$toast({
              duration: 1000,       // 持续展示 toast
              message: '请输入最后月供金额'
            });
            return;
          }

          data.question = 5;
          data.lastHouseLoan = endMoney;
        }

        if (currentIndex === 5) {
          this.handleGoBackApp();
          return;
        }

        let countMoney = getMoneyForLoanable(data);
        if (isNaN(countMoney)) {
          this.$toast({
            duration: 1000,       // 持续展示 toast
            message: countMoney
          });
          return;
        }
        countMoney=parseFloat(countMoney).toLocaleString();
        service.setData('money', countMoney);
        this.$router.push({
          name: 'Apply',
          params: {money: countMoney}
        })
      },

      /**
       * 跳回App
       */
      handleGoBackApp() {
        let data = {
          type: 'appAction',
          actionData: {
            action_type: 'app',
            action_uri: 'searchLoanResult'
          }
        };
        window.postMessage(JSON.stringify(data), '*');
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .van-nav-bar .van-icon {
    color: #2c3e50;
    font-size: 1.2rem;
  }

  .container {
    padding: 0.8rem;
    background: linear-gradient(#ef5066, #f69981);
  }

  .nextBtn {
    margin-top: 1.2rem;
  }

  .nextBtn .van-button--danger {
    color: #fff;
    background-color: #ef5066;
    border: 0.0625rem solid #ef5066;
    width: 100%;
    height: 3rem;
    line-height: 3rem;
  }

  .containerHeader {
    width: calc(100% - 2rem);
    background: #fff;
    -webkit-border-radius: 0.3rem;
    -moz-border-radius: 0.3rem;
    border-radius: 0.3rem;
    padding: 2rem 1rem 1.6rem 1rem;
  }

  .productInfo {
    display: flex;
  }

  .productInfo img {
    width: 5rem;
    height: 2rem;
  }

  .productInfo .productName {
    margin-left: 1rem;
    color: rgba(0, 0, 0, .87);
    font-size: 1.1rem;
    font-weight: bold;
    -ms-text-overflow: ellipsis;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
  }

  .productIntroduce {
    font-size: 0.9rem;
    color: #999;
    margin-top: 0.8rem;
    text-align: left;
    line-height: 1.3rem;
  }

  .chooseContainer {
    background: #fff;
    margin-top: 1.1rem;
    -webkit-border-radius: 0.3rem;
    -moz-border-radius: 0.3rem;
    border-radius: 0.3rem;
    padding: 1.5rem 1rem;
    text-align: left;
  }

  .chooseTitle {
    font-size: 1rem;
    color: rgba(0, 0, 0, .87);
    font-weight: 500;
    -ms-text-overflow: ellipsis;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
  }

  .chooseList {
    font-size: 0.9rem;
    color: #555;
  }

  .chooseList .chooseListItem {
    margin-top: 1rem;
  }

  .btnList {
    margin-top: 0.5rem;
  }

  .btnList .van-button--default {
    margin-top: 0.5rem;
    padding: 0 3rem;
    height: 2.2rem;
    line-height: 2.2rem;
    border: 0.0625rem solid #ddd;
    margin-right: 0.3rem;
  }

  .btnList .van-button--default.active {
    background-color: #ef5066;
    border: 0.0625rem solid #ef5066;
    color: #fff;
  }

  .formItem {
    display: flex;
    height: 3rem;
    line-height: 3rem;
  }

  .formItem:first-child {
    margin-top: 0.5rem;
  }

  .formItem label {
    width: 5rem;
    font-size: 0.9rem;
    color: rgba(0, 0, 0, 0.87);
  }

  .formItem .formValue {
    width: calc(100% - 6.1rem);
    text-align: right;
    font-size: 0.9rem;
    -ms-text-overflow: ellipsis;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
  }

  .formItem img {
    width: 1.1rem;
    height: 1.1rem;
    margin-top: 0.85rem;
  }

  .formItem .formInput {
    width: calc(100% - 5rem);
    margin-top: 0.15rem;
  }

  .formItem1 {
    display: flex;
    height: 3rem;
    line-height: 3rem;
  }

  .formItem1:first-child {
    margin-top: 0.5rem;
  }

  .formItem1 label {
    width: 8.5rem;
    font-size: 0.9rem;
    color: rgba(0, 0, 0, 0.87);
  }

  .formItem1 .formValue {
    width: calc(100% - 9.6rem);
    text-align: right;
    font-size: 0.9rem;
    -ms-text-overflow: ellipsis;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
  }

  .formItem1 img {
    width: 1.1rem;
    height: 1.1rem;
    margin-top: 0.85rem;
  }

  .formItem1 .formInput {
    width: calc(100% - 8.5rem);
    margin-top: 0.15rem;
  }

</style>



