<template>
  <div class="hello">
    <van-nav-bar
      title="贷款申请成功"
    />
    <div class="successContainer">
      <img src="../assets/successLogo.png" alt=""/>
      <div class="successText">贷款申请成功</div>
    </div>
    <div class="scheduleContainer">
      <div class="scheduleContainerHeader">申请进度</div>
      <div class="scheduleItem">
        <div class="scheduleTitle">
          <img src="../assets/schedule.png" alt=""/>
          <div class="scheduleTitleText">申请2017-11-12</div>
        </div>
        <div class="scheduleItemContent">
          您的申请已成功提交。
        </div>
        <div class="blackDiv active"></div>
      </div>
      <div class="scheduleItem">
        <div class="scheduleTitle">
          <img src="../assets/schedule.png" alt=""/>
          <div class="scheduleTitleText">普惠通专员受理</div>
        </div>
        <div class="scheduleItemContent">
          您的新一贷贷款订单将于普惠通客服专员进行回访，待信息确认正确后，平台银行业务人员会进行下一步调查。
        </div>
        <div class="blackDiv"></div>
      </div>
      <div class="scheduleItem">
        <div class="scheduleTitle">
          <img src="../assets/schedule1.png" alt=""/>
          <div class="scheduleTitleText">银行业务人员受理</div>
        </div>
      </div>
    </div>
    <div class="nextBtn">
      <van-button type="danger" @click="handleSuccess">完成</van-button>
    </div>
  </div>
</template>

<script>
  import Vue from 'vue';
  import {Button, Step, Steps} from 'vant';

  Vue.use(Button);
  Vue.use(Step).use(Steps);
  export default {
    name: 'Success',
    data() {
      return {
        active: 0
      }
    },
    methods: {
      /**
       * 处理完成事件
       */
      handleSuccess() {
        let data = {
          type: 'appAction',
          actionData: {
            action_type: 'app',
            action_uri: 'searchLoanResult'
          }
        };
        window.postMessage(JSON.stringify(data), '*');
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .nextBtn {
    margin-top: 1.2rem;
  }

  .nextBtn .van-button--danger {
    color: #fff;
    background-color: #ef5066;
    border: 0.0625rem solid #ef5066;
    width: 100%;
    height: 3rem;
    line-height: 3rem;
  }

  .successContainer {
    margin-top: 2rem;
  }

  .successContainer img {
    width: 8.2rem;
    height: 7.2rem;
  }

  .successContainer .successText {
    font-size: 1.6rem;
    font-weight: 500;
    margin-top: 0.5rem;
  }

  .scheduleContainer {
    text-align: left;
    padding: 1rem;
  }

  .scheduleContainerHeader {
    margin-bottom: 1rem;
    font-weight: 500;
  }

  .scheduleTitle {
    display: flex;
  }

  .scheduleItem {
    margin-left: 1rem;
  }

  .scheduleTitle img {
    width: 1.1rem;
    height: 1.1rem;
    margin-top: 0.15rem;
  }

  .scheduleTitleText {
    margin-left: 0.8rem;
    font-weight: 500;
  }

  .scheduleItemContent {
    margin-left: 0.5rem;
    border-left: 0.093rem solid #ef5066;
    padding-left: 1.3rem;
    color: #999;
  }

  .blackDiv {
    height: 2rem;
    margin-left: 0.5rem;
    border-left: 0.093rem solid #999;
  }

  .blackDiv.active {
    border-left: 0.093rem solid #ef5066;
  }
</style>



