import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/views/Index'
import Apply from '@/views/Apply'
import Success from '@/views/Success'

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Index',
      component: Index
    },
    {
      path: '/index',
      name: 'Index1',
      component: Index
    },
    {
      path: '/apply',
      name: 'Apply',
      component: Apply
    },
    {
      path: '/success',
      name: 'Success',
      component: Success
    }
  ]
})



